
$height : 80px;
@import '../../gobal';

.sidebar-container {
    flex: 1.3;
    width: 100%;
    height: 100%;
    display: flex;
    position:sticky;
    top: 70px;
    padding-bottom: 10px;
    z-index: 1;
    margin-top: 10px;
    
    
    .sidebar{
        width: 80%; //
        height:calc(100vh - $height);
        background-color: $mainColor;
        color: $textColor;
        display: flex;
        position: relative;
        transition: all 1s ease;
        padding-top: 10px;
        padding-bottom: 10px;
        cursor: pointer;
        overflow-y: scroll;
        &::-webkit-scrollbar{
            display: none;
        }
      
        

        
        .sidebar-item{
            display: flex;
            flex-direction: column;
            gap: 10px;
            width: 100%;
            position: relative;
            margin: 0 auto;
            transition: all 1s ease;
            
            .avatar{
                display: flex;
                align-items: center;
                justify-content: flex-start;
                gap: 10px;
                padding: 5px;
                position: relative;
                transition: all 1s ease;
                width: 100%;
                
                img{
                    width: 45px;//
                    height: 45px;//
                    border-radius: 50%;
                    object-fit: cover;
                    border: 1px solid $textColor;
                    padding: 2px;
                    margin-left: 10px;//
                    
                    
                }
                .name{
                    transition: all 1s ease;
                    display: flex;
                    flex-direction: column;
                    word-break: keep-all;
                    text-align: left;
                    opacity: 1; //  
               
                    .name-items{
                        // max-width: 250px;
                        font-size: 25px;
                        font-weight: 700;
                        text-overflow:ellipsis;
                        overflow: hidden;
                    
                       
                        
                        
                    }
                    .name-status{
                        font-size: 12px;
                        font-weight: 200;
                        
                      
                    }
                    
                    
                    
                }
                .status{
                    position: absolute;
                    width: 15px;//
                    height: 15px;//
                    border-radius: 50%;
                    background-color: rgb(67, 204, 67);
                    left: 45px;//
                    bottom: 12px;

                }
                
                
            }
            .line{
                border-bottom: 1px solid $textColor;
                width: 90%;
                opacity: 0.4;
                margin-left: 2px;
                margin-top: 10px;
                

            }
            
          
            .top-side{
                margin-top: 15%;
                justify-content: space-between;
                padding: 0 25px;
                display: flex;
                flex-direction: column;
                gap: 30px;  
                                              
                .top{   
        
                    margin: 0 10px;
                    align-items: center;
                    display: flex;
                    margin: 0 auto;
                    justify-content: flex-start;
                    gap: 10px;  
                    
                    .top-icon{
                        margin-left: 10px;
                        color: $textColor;
                        font-size: 25px;
                        padding:0 8px;
                        padding-top: 5px;

                    }
                    span{
                        transition: all 0.5s ease;
                        font-size: 25px;
                        opacity: 1; //
                        color: $textColor;
                        text-decoration: none;
                        font-weight: 700;
                        
                        
                    }
                    &:hover {
                        transform: scale(1.1);
                        border-radius: 10px;
                        background-color: $softColor;
                        
                        
                    }
                    &.active{
                        background-color:$softColor;
                       
                        border:1px solid #424242;
                        border-radius: 10px;
                        .top-icon{
                            color: $textColor;
                        }
                        span{
                            color: $textColor;
                        }
                    }  
                }
            }
            


        }
        .touch{
            background-color: $textColor;
            position: absolute;
            width: 5%;
            height: 10%;
            bottom: 50%;
            left: 95%;
            margin: 0 auto;
            border-radius: 25px;
            cursor: pointer;
            opacity: 0; //
           


        }
