                        }
                                
                   }
                     
                 }
                
            

        }
    


    }


    @media (max-width:768px) {
        display: none;
        margin: 0;
        padding: 0;
        z-index: 10;
        top: 60px;
        height: calc(100vh - 60px);
        .sidebar{
            width: 100%;
            height: 100%;
            .sidebar-item{
                .top-side{
                    margin: 0;
                }
            }
        }
       
    }
   
    
